import React, { Fragment } from 'react';
import { useForm } from "react-hook-form";

const JoinForm = () => {
  const { register, handleSubmit, errors } = useForm({
    mode: "onBlur"
  });
  const onSubmit = data => console.log(data);
  console.log("error", errors);

  return (
    <>
      <style type="text/css">
        {`
      .check-student{
        text-align: right;
      }

      .form-select{
        width: 100%;
        min-height: 56px;
        padding: 3px 20px;
        color: #748494;
        border: 1px solid #F5F5F5;
        border-radius: 5px;
        outline: none;
        background-color: #F5F5F5;
      }
      `}
      {/* css  ?? */}
      </style>
      <Fragment>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="col-md-12 col-12 mb-4">
            <p></p>
            <input type="email" placeholder="Email *" name="email" ref={register({
              required: ' ',
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                message: " "
              }
            })} />
            {errors.email && <p>{errors.email.message}</p>}
          </div>

          <p></p>
          <div className="col-md-12 col-12 mb-4">
            <input type="password" placeholder="Pssword *" name="password" 
            ref={register({
              required: " ", 
              minLength: 8, 
              maxLength: 20,
              pattern: {
                value: /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]$/,
                message: " , ,   8~20"
              }
              }) }/>
              {errors.password && <p>{errors.password.message}</p>}

          </div>
          <div className="col-md-12 col-12 mb-4">
            <input type="password" placeholder="Check Pssword *" name="check-password" />
          </div>

          <p></p>
          {/*  */}
          <div className="col-md-12 col-12 mb-4">
            <input type="text" placeholder="Your Name *" name="name" ref={register({ required: 'Name is required' })} />
            {errors.name && <p>{errors.name.message}</p>}
          </div>
          {/*  */}
          <div className="col-md-12 col-12 mb-4">
            <input type="text" placeholder="PhoneNumber * ex)01012341234" name="ph" />
          </div>

          <div className="check-student">
            <input type="checkbox" name="isStudent" />
            <label>&nbsp;</label>
          </div>
          <p></p>
          <div className="col-md-12 col-12 mb-4">
            {/*  */}
            <select className="form-select">
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option>ICT</option>
              <option></option>
              <option></option>
            </select>
          </div>

          <div className="col-md-12 col-12 mb-4">
            {/*  */}
            <select className="form-select">
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
              <option></option>
            </select>
          </div>
          {/*  */}
          <div className="col-md-12 col-12 mb-4">
            <select className="form-select">
              <option>1</option>
              <option>2</option>
              <option>3</option>
              <option>4</option>
            </select>
          </div>

          <div className="col-md-12 col-12 mb-4">
            <input type="text" placeholder=" * ex)60123456" name="school-id" />
          </div>

          {/* submit   */}
          <div className="col-12 text-center mb-4">
            <button className="btn btn-primary btn-hover-secondary"></button>
          </div>
        </form>
        <p className="form-messege"></p>
      </Fragment>
    </>
  )
}

export default JoinForm;
